.exhibitor__title,
.buyer__title {
  margin: 0;
  font-size: 24px;
  color: $color-blue;
  font-weight: 300;
  padding-bottom: 13px;
}

.exhibitor__text,
.buyer__text {
  margin: 0;
  font-size: 16px;
  color: #606060;
  font-weight: 600;
  padding-bottom: 13px;
  height: 30%;
}

.exhibitor__info,
.buyer__info {
  margin: 0;
  font-size: 12px;
  color: $color-nobel;
  font-weight: 300;
  padding-bottom: 25px;
  height: 33%;
}

.exhibitor__btn,
.buyer__btn {
  display: flex;
  font-size: 20px;
  font-weight: 300;
  border: 1px solid $color-blue;
  color: $color-blue;
  width: 222px;
  height: 50px;
  align-items: center;
  align-self: center;
  justify-content: center;
  outline: none;

  &:hover,
  &:focus {
    opacity: 0.8;
  }
}

/*  */
.exhibitor-reg {
  display: flex;
  flex-wrap: wrap;

  &__block {
    display: flex;
  }
}

.exhibitor-reg__title {
  width: 80%;
  margin: 0;
  padding-top: 29px;
  font-size: 38px;
  font-weight: 300;
  line-height: 30px;
  color: $color-blue;
}

.exhibitor-reg__btn {
  width: 20%;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-end;

  span {
    padding-left: 5px;
  }
}
