.footer {
  display: grid;
  grid-template-areas:
    "logotip mailing menu"
    "info info info ";
  grid-column-gap: 10px;
  border-top: 1.5px solid $color-blue;
  padding-top: 25px;
  margin-top: 57px;

  @include viewport--sm {
    display: grid;
    grid-template-areas:
      "menu"
      "mailing"
      "logotip"
      "info";
    justify-content: normal;
    margin-top: 20px;
  }
}

.footer__logotip {
  grid-area: logotip;

  @include viewport--sm {
    justify-content: space-around;
  }
}

.footer__mailing {
  grid-area: mailing;
  margin-right: 0;

  @include viewport--sm {
    width: 100%;
    padding-bottom: 40px;
  }
}

.footer__menu {
  grid-area: menu;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;

  @include viewport--sm {
    width: 100%;
    justify-content: space-between;
    padding-bottom: 22px;
  }
}

.footer__info {
  grid-area: info;
  width: 100%;
  display: flex;
  justify-content: center;
  color: $color-nobel;
  font-size: 12px;
  margin-top: 30px;
  margin-bottom: 45px;

  @include viewport--sm {
    margin-top: 29px;
    margin-bottom: 31px;
  }
}
